import React, {useState} from "react";
import { SafeAreaView, ScrollView, StyleSheet } from "react-native";
import { DataStructure } from "./types/treeData";
import List from "./components/List";
import Button from "./components/Button";

interface PillProps {
    data: DataStructure[];
}

const Pill: React.FC<PillProps> = ({ data }) => {
    const [selected, setSelected] = useState<number | null>(null);

    return (
        <SafeAreaView style={styles.container}>
            <ScrollView>
                <List<{ id: number; name: string }>
                    treeData={data}
                    selectedItemId={selected}
                    buttonComponent={(itemData) => {
                        const isSelected = itemData.id === selected;

                        return (
                            <Button
                                onPress={() => {
                                    setSelected(isSelected ? null : itemData.id);
                                }}
                                isSelected={isSelected}
                                title={`${itemData?.name} + ${itemData.children?.length ?? 0}`}
                            />
                        );
                    }}
                />
            </ScrollView>
        </SafeAreaView>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
    },
});

export default Pill